html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Leelawadee";
  background-color: #000;
}

p {
  font-size: 2rem;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 5rem/2 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  color: white;
  font-weight: bold;
  margin-bottom: 5rem;
  text-align: center;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

@font-face {
  font-family: Leelawadee;
  src: url("/build/fonts/LEELAWAD.TTF"), url("/build/fonts/LEELAWDB.TTF");
}
@font-face {
  font-family: Silka;
  src: url("/build/fonts/silka-regular-webfont.ttf");
}
.ml-2 {
  margin-left: 2rem;
}

.cl-white {
  color: #fff;
}

.cl-grey {
  color: #545454;
}

.bg-secondary {
  background-color: #131313 !important;
}

.c-white {
  color: #fff;
}

.c-black {
  color: #0A192F;
}

.boton-menu {
  font-size: 2.5rem;
}

.arg-p,
.language {
  display: none;
}

@media (min-width: 768px) {
  .arg-p,
.language {
    display: block;
    font-size: 1.8rem;
  }
}
.logo {
  height: 4rem;
  width: 14rem;
  margin-left: 2rem;
}

.logo-ff {
  position: fixed;
  left: 50%;
  top: 2rem;
}
.logo-ff img {
  width: 5rem;
  height: 100%;
}

.navegacion {
  margin-right: 2rem;
}
.navegacion a {
  font-weight: 700;
}

.social-nav {
  width: 20px;
  top: 50vh;
  transform: translate(0, -50%);
  right: 40px;
  position: fixed;
  z-index: 25;
}
.social-nav a:hover {
  color: #B28550;
}

.hero {
  background-image: url(/build/img/Heade.svg);
  background-color: #fff;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero .contenido-hero {
  margin: 0 2rem;
}
.hero .contenido-hero p {
  text-align: center;
  font-size: 4rem;
  font-weight: 800;
}
.hero .contenido-hero .dev-text {
  font-family: "Silka", sans-serif;
  font-size: 2rem;
  font-weight: 500;
}
.hero .contenido-hero .welcome-text {
  color: #B28550;
}
@media (min-width: 768px) {
  .hero .contenido-hero p {
    font-size: 7.2rem;
  }
  .hero .contenido-hero .dev-text {
    letter-spacing: 0.5rem;
    font-size: 2rem;
    font-weight: 500;
  }
}

.arrow {
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 90%;
  transform-origin: 90% 90%;
  transform: translate3d(-90%, -90%, 0);
}

.arrow-first {
  animation: arrow-movement 2s ease-in-out infinite;
}

.arrow-second {
  animation: arrow-movement 2s 1s ease-in-out infinite;
}

.arrow:before,
.arrow:after {
  background: #131212;
  content: "";
  display: block;
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
}

.arrow:before {
  transform: rotate(45deg) translateX(-23%);
  transform-origin: top left;
}

.arrow:after {
  transform: rotate(-45deg) translateX(23%);
  transform-origin: top right;
}

@keyframes arrow-movement {
  0% {
    opacity: 0;
    top: 85%;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.about .background-aboutme {
  background-image: url(../../../build/img/aboutme.png);
  background-color: #fff;
  height: 100vh;
  background-position-x: 30%;
  background-repeat: no-repeat;
  background-size: cover 15rem;
}
@media (min-width: 768px) {
  .about .background-aboutme {
    background-position: center;
  }
}
.about .bottom-fade {
  width: 100%;
  top: 140%;
  height: 60%;
  float: left;
  position: absolute;
  z-index: 23;
  background: linear-gradient(0deg, black 10%, rgba(0, 0, 0, 0) 100%);
}
.about .text-about {
  padding: 0 3rem;
  font-size: 1rem;
  position: relative;
  top: -15rem;
  z-index: 24;
}
.about .text-about p {
  max-width: 80rem;
  text-align: center;
  margin: 0 auto;
  color: white;
  line-height: 4rem;
}
.about .text-about p span {
  font-weight: bold;
  background-color: transparent !important;
}
.about .text-about .text-normal-background {
  background-color: black;
  margin-bottom: 2.5rem;
}

.technologies {
  background-image: url(../../../build/img/Header_Black-01.svg);
  background-size: cover;
  position: relative;
  z-index: 25;
  margin-bottom: 5rem;
}

.skills h3 {
  color: white;
  text-align: start;
  padding: 0 0 0 2rem;
  margin: 0;
}
.skills ul {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.skills li {
  min-width: 11rem;
  height: 12rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 2em;
  margin-top: 2em;
  transition: 0.2s;
  background-color: #202020;
  border-radius: 5px;
  padding: 1em;
}
.skills li p {
  margin: auto 0 0 0;
}
.skills img {
  width: 5rem;
  margin: auto;
}
.skills p {
  color: white;
}

.projects {
  background-color: #fff;
  background-image: url(../../../build/img/Header.svg);
  background-size: cover;
  padding: 2rem 0 10rem 0;
}

.cards {
  width: 100%;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  padding: 0 5rem;
  max-width: 140rem;
  margin: 0 auto;
  color: #fff;
}

@media (min-width: 768px) {
  .cards {
    grid-template-columns: repeat(5, 1fr);
  }
}
.card--1 .card__img, .card--1 .card__img--hover {
  background-image: url(../../../build/img/webs/1.webp);
}

.card--2 .card__img, .card--2 .card__img--hover {
  background-image: url(../../../build/img/webs/2.webp);
}

.card__like {
  width: 18px;
}

.card__clock {
  width: 15px;
  vertical-align: middle;
  fill: #AD7D52;
}

.card__time {
  font-size: 12px;
  color: #AD7D52;
  vertical-align: middle;
  margin-left: 5px;
}

.card__clock-info {
  float: right;
}

.card__img {
  visibility: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card__img--hover {
  transition: 0.2s all ease-out;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  position: absolute;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  top: 0;
}

.card {
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #202020;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

@media (min-width: 768px) {
  .card:hover {
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.1, 1.1);
  }
}
.card__info {
  z-index: 2;
  background-color: #202020;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card__category {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #fff;
}

.card__title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Roboto Slab", serif;
}

.card__by {
  font-size: 12px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.card__author {
  font-weight: 600;
  text-decoration: none;
  color: #AD7D52;
}

@media (min-width: 768px) {
  .card:hover .card__img--hover {
    height: 100%;
    opacity: 1;
  }

  .card:hover .card__title, .card:hover .card__category {
    display: none;
  }

  .card:hover .card__info {
    background-color: transparent;
    position: relative;
  }

  .card:hover .card__info-hover {
    opacity: 1;
  }
}